.auth__submit{
    width: 358px;
    height: 50px;
    padding: 0;
    background-color: white;
    color: black;
    font-family: Inter, Arial, sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 22px;
    border: 0;
    border-radius: 2px;
    cursor: pointer;
    transition: opacity 0.3s ease;
    margin:216px 0 0 0;
}

.auth__submit:hover{
    opacity: 0.85;
}